@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500&display=swap');

html {
    box-sizing: border-box; }

*,
*::before,
*::after {
    box-sizing: inherit; }

body {
    min-width: 320px;
    margin: 0;
    font-family: Poppins, sans-serif;
    font-weight: 400px;
    line-height: 1;
    background-color: #f7f7f7;
    color: #333333; }

a {
    text-decoration: none;
    color: inherit; }

ul {
  list-style: none;
  padding: 0;
  margin: 0; }


button, input {
  font: inherit; }


h1, h2, h3, p, ul {
    padding: 0;
    margin: 0; }


.container {
  padding-left: 75px;
  padding-right: 75px;
  max-width: 1920px;
  margin: auto; }


.visually-hidden {
  position: absolute !important;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden; }


